import '../css/content.css';

import {useEffect, useRef} from 'react';
import Button from './common/Button';
import {findWayInKakao, findWayInNaver} from '../utils/map';
import {AWS_CDN_URL, SHUTTLE_URL} from '../constants';

const SPOT_LAT = 37.5959568282511;
const SPOT_LNG = 127.610119081614;
const SPOT_ID = 13087095;
const SPOT_NAME = ' ';

function LocationSection() {
  const mapRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const kakaoMaps = (window as any).kakao?.maps;
    if (mapRef.current && kakaoMaps) {
      const map = new kakaoMaps.Map(mapRef.current, {
        center: new kakaoMaps.LatLng(SPOT_LAT, SPOT_LNG),
	      level: 4,
        draggable: false,
      });

      const marker = new kakaoMaps.Marker({
        map: map, 
        position: new kakaoMaps.LatLng(SPOT_LAT, SPOT_LNG),
      });

      const onClose = () => overlay.setMap(null);
      
      const overlay = new kakaoMaps.CustomOverlay({
        content: getOverlayContent(onClose),
        position: marker.getPosition(),    
      });

      kakaoMaps.event.addListener(marker, 'click', () => {
        overlay.setMap(map);
      });
    }
  }, []);

  const getOverlayContent = (closeFn: any) => {
    const content = document.createElement('div');
    content.setAttribute('class', 'wrap');

    const info = document.createElement('div');
    info.setAttribute('class', 'info');

    const title = document.createElement('div');
    title.setAttribute('class', 'title');
    title.innerText = SPOT_NAME;

    const closeButton = document.createElement('div');
    closeButton.setAttribute('class', 'close');
    closeButton.addEventListener('click', closeFn);

    title.appendChild(closeButton);

    const body = document.createElement('div');
    body.setAttribute('class', 'body');
    body.innerHTML = `
      <div class="desc">
        <div class="ellipsis">    777</div>
        <div class="jibun ellipsis">()   604-3</div>
      </div>
    `;

    info.appendChild(title);
    info.appendChild(body);
    content.appendChild(info);

    return content;
  };

  const openShuttleTicket = () => {
    window.open(SHUTTLE_URL);
  };

  return (
    <div className="mb-32 text-center text-2xl font-custom">
      <div className="mb-16">
        <h1 className="text-4xl font-bold mb-8">
           ~ 
        </h1>
        <div>
          <p className="font-bold underline"></p>
          <p>  (   777)</p>
          <p className="text-xl">*    *</p>
        </div>
        <div className="m-8">
          <div ref={mapRef} className="h-96 w-full" />
        </div>
        <div className="flex justify-center gap-x-2 my-4">
          <Button onClick={() => findWayInKakao(SPOT_ID)}>
             
          </Button>
          <Button onClick={() => findWayInNaver({lat: SPOT_LAT, lng: SPOT_LNG, dname: SPOT_NAME})}>
             
          </Button>
        </div>
      </div>

      <div className="mb-16">
        <h2 className="text-3xl font-bold mb-8">
          
        </h2>
        <div className="mb-8">
          <p>      !</p>
          <p>   ,</p>
          <p>      .</p>
        </div>
        <div className="m-8">
          <img src={`${AWS_CDN_URL}/parking-zone.jpeg`} alt="Parking Zone" />
        </div>
        <div className="m-8">
          <p>{` ,   >3<`}</p>
          <p>
            (   , <span className="underline">546</span>)
          </p>
        </div>
      </div>

      <div className="mb-16">
        <div className="mb-8">
          <h2 className="text-3xl font-bold">
             
          </h2>
          <p className="text-xl">()</p>
        </div>
        <div className="mb-8">
          <div className="mb-4">
            <p className="font-bold underline"></p>
            <p>2022.10.15 14:00</p>
          </div>
          <div className="mb-4">
            <p className="font-bold underline"></p>
            <p> 2</p>  
            <p>    .</p>  
            <p className="text-xl">
              (<span className="underline">" "</span>    18 )
            </p>
          </div>
          <div className="mb-4">
            <p className="font-bold underline"></p>
            <p>20  </p>  
            
            <p>2130  </p>
          </div>
        </div>
        <Button onClick={openShuttleTicket}>
           
        </Button>
      </div>
    </div>
  );
}

export default LocationSection;
